.todo {
    width: 250px;
    height: 100px;
    font-size: 23px;
    background-color: rgba(235, 236, 240, 1.00);
    margin-bottom: 30px;
    margin-right: 30px;
    border-radius: 5px;
    box-shadow: 1px 1px 5px black;
    padding: 10px;
    font-weight: 700;
    color: black;
    transition: 0.5s;
}

.container {
    display: flex;
    align-items: center;
}

.title {
    margin-left: 5px;
}

.todo:hover {
    transition: 0.5s;
    background-color: rgb(213, 214, 218);
}
